import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Spinner } from 'reactstrap'
import {
  Row,
  Col,
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Card,
  FormGroup,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Form,
  Alert,
  Button,
  CardHeader,
  CardBody,
  CardTitle,
  Label
} from 'reactstrap'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Select from 'react-select'
import { toast } from 'react-toastify'
import { Eye, EyeOff, Delete, Trash, ChevronDown } from 'react-feather'
import 'react-toastify/dist/ReactToastify.css'
import { ToastContainer } from 'react-toastify'
import '../../assets/scss/plugins/extensions/toastr.scss'
import Breadcrumbs from '../../components/@vuexy/breadCrumbs/BreadCrumb'
import classnames from 'classnames'
import AddAssets from './AddAssets'
import GoalOverview from '../ui-elements/cards/analytics/GoalOverview'
import { useDropzone } from 'react-dropzone'

import themeConfig from '../../configs/themeConfig'

import '../../assets/scss/plugins/extensions/dropzone.scss'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";

const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

const colourOptions1 = [
  {
    value: 'Bank',
    label: 'Bank',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Email',
    label: 'Email',
    color: '#0052CC',
    isFixed: true
  },
  {
    value: 'Card',
    label: 'Card',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Social Media',
    label: 'Social Media',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'HealthCare',
    label: 'HealthCare',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'App',
    label: 'App',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Goverment Portal',
    label: 'Goverment Portal',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Office Portal',
    label: 'Office Portal',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Educational Portal',
    label: 'Educational Portal',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'E-commerce',
    label: 'E-commerce',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Finance',
    label: 'Finance',
    color: '#00B8D9',
    isFixed: true
  },
  { value: 'Custom', label: 'Custom', color: '#0052CC', isFixed: true }
]
class PasswordVault extends React.Component {
  state = {
    isLoading: false,
    showPass: false,
    active: '1',
    modal: false,
    pwd: '',
    ptype: '',
    userName: '',
    accountNumber: '',
    description: '',
    confirmPwd: '',
    saved: [],
    selectAssetRef: React.createRef()
  }

  componentWillMount() {
    axios
      .post('/backendapi/pwd/list', {
        user: JSON.parse(localStorage.getItem('logInUserData'))._id
      })
      .then((res) => {
        this.setState({ saved: res.data })
      })
  }

  toggleModal = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
      confirmPwd: '',
      showIdx: ''
    }))
  }

  toggleModal2 = () => {
    let passwordShowTime = JSON.parse(
      localStorage.getItem('logInUserData')
    ).pwdtime
    if (passwordShowTime > 10 || passwordShowTime < 2) {
      passwordShowTime = 3
    }

    if (this.state.confirmPwd === '123') {
      console.log('this.state.showIdx', this.state.showIdx)
      this.togglepwdshow(true, this.state.showIdx)
      setTimeout(
        function () {
          this.togglepwdshow(false, this.state.showIdx)
          this.setState({ showPass: false })
        }.bind(this),
        passwordShowTime ? passwordShowTime * 1000 : 3000
      )
      this.setState((prevState) => ({
        modal: !prevState.modal,
        confirmPwd: ''
      }))
    } else {
      toast.warning('Wrong Password')
    }
  }

  clearSaved = (e) => {
    this.state.selectAssetRef.current.select.clearValue()
    this.setState({
      pwd: '',
      ptype: '',
      userName: '',
      accountNumber: '',
      description: ''
    })
  }
  addToSaved = (e) => {
    this.setState({ isLoading: true })
    e.preventDefault()
    const {
      saved,
      ptype,

      pwd,
      userName,
      accountNumber,
      description
    } = this.state

    if (ptype === '' || pwd === '' || accountNumber === '') {
      toast.warning('Mandatory Field Missing !!')
      this.setState({ isLoading: false })
    } else {
      const newp = {
        pwdname: accountNumber,
        pwd,
        showPass: false,
        userName: userName,
        accountNumber: accountNumber,
        description: description
      }
      const newp1 = {
        pwdname: accountNumber,
        pwd,
        ptype,
        userName: userName,
        user: JSON.parse(localStorage.getItem('logInUserData'))._id
      }

      saved.push(newp)
      this.state.selectAssetRef.current.select.clearValue()
      this.setState({
        saved,
        pwd: '',
        ptype: '',
        userName: '',
        accountNumber: '',
        description: ''
      })
      axios.post('/backendapi/savepwd', newp1).then((res) => {
        //this.setState({saved:res.data});
        this.setState({ isLoading: false })
        toast.success('Password saved successfully')
      })
    }
  }

  togglepwdshow = (b, i) => {
    const { saved, showPass } = this.state

    console.log(b, i)
    saved[i].showPass = b

    this.setState({ saved, showPass: b })
  }

  deletepwd = (i) => {
    let { saved } = this.state

    axios.post('/backendapi/pwd/delete', saved[i]).then((res) => {
      //this.setState({saved:res.data});

      toast.warning('Password deleted successfully')
      saved = saved.filter((ex) => {
        return ex._id !== saved[i]._id
      })
      this.setState({ saved })
    })
  }

  render() {
    let passwordShowTime = JSON.parse(
      localStorage.getItem('logInUserData')
    ).pwdtime
    if (passwordShowTime > 10 || passwordShowTime < 2) {
      passwordShowTime = 3
    }
    return (
      <React.Fragment>
        <Modal
          isOpen={this.state.modal}
          toggle={this.toggleModal}
          className="modal-dialog-centered"
          fade={false}
        >
          <ModalHeader
            style={{ border: 'solid 1px' }}
            toggle={this.toggleModal}
          >
            Confirm your identity
          </ModalHeader>
          <ModalBody style={{ border: 'solid 1px' }}>
            Enter your login password to continue
            <br />
            <br />
            <FormGroup>
              {/* <Label for="password">Password:</Label> */}
              <Input
                type="password"
                id="password"
                placeholder="Password"
                onChange={(e) => {
                  this.setState({ confirmPwd: e.target.value })
                }}
              />
            </FormGroup>
            <Alert className="mt-1" color="danger">
              <span>
                Your password will only be visible for {passwordShowTime}{' '}
                seconds!
              </span>
            </Alert>
          </ModalBody>
          <ModalFooter>
            <Button
              color={this.state.confirmPwd === '' ? 'primary' : 'success'}
              disabled={this.state.confirmPwd === ''}
              onClick={this.toggleModal2}
            >
              Accept
            </Button>{' '}
          </ModalFooter>
        </Modal>
        <h2 className="warning">Password Vault</h2>
        {/*
        <Breadcrumbs
          breadCrumbTitle="My Vault"
          breadCrumbParent="Password"
          breadCrumbActive="Vault"
        /> */}
        <Row>
          <Col lg="8" md="6" sm="12">
            <div style={{ width: window.screen.width < 500 ? '100%' : '150%' }}>
              <Row>
                <Col sm="12">
                  <TabContent activeTab={this.state.active}>
                    <TabPane tabId="1">
                      <Row>
                        <ToastContainer />
                        <Col lg="8" md="6" sm="12">
                          <Card>
                            <CardHeader>
                              <CardTitle>Save new Password</CardTitle>
                            </CardHeader>
                            <CardBody>
                              <Form>
                                <Row>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Input
                                        className="input-label"
                                        type="text"
                                        name="accountNumber"
                                        id="accountNumberMulti"
                                        placeholder="Password For *"
                                        value={this.state.accountNumber}
                                        onChange={(e) => {
                                          this.setState({
                                            accountNumber: e.target.value
                                          })
                                        }}
                                      />
                                      <Label
                                        className={
                                          themeConfig.theme === 'dark'
                                            ? 'dark-label'
                                            : 'light-label'
                                        }
                                        for="accountNumberMulti"
                                      >
                                        Password For *
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Input
                                        className="input-label"
                                        type="text"
                                        name="userName1"
                                        id="userNameMulti1"
                                        placeholder="User Name"
                                        value={this.state.userName}
                                        onChange={(e) => {
                                          this.setState({
                                            userName: e.target.value
                                          })
                                        }}
                                      />
                                      <Label
                                        className={
                                          themeConfig.theme === 'dark'
                                            ? 'dark-label'
                                            : 'light-label'
                                        }
                                        for="userNameMulti"
                                      >
                                        User Name
                                      </Label>
                                    </FormGroup>

                                    {/* <FormGroup className="form-label-group">
                                      <Input
                                      className='input-label'
                                                                            
                                        type="text"
                                        name="description"
                                        id="descriptionMulti"
                                        placeholder="Note"
                                        value={this.state.description}
                                        onChange={(e) => {
                                          this.setState({
                                            description: e.target.value,
                                          });
                                        }}
                                      />
                                      <Label className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              } for="descriptionMulti">Note</Label>
                                    </FormGroup> */}
                                  </Col>

                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Input
                                        className="input-label"
                                        type="password"
                                        name="name"
                                        id="nameMulti"
                                        placeholder="Password *"
                                        value={this.state.pwd}
                                        onChange={(e) => {
                                          this.setState({
                                            pwd: e.target.value
                                          })
                                        }}
                                      />
                                      <Label
                                        className={
                                          themeConfig.theme === 'dark'
                                            ? 'dark-label'
                                            : 'light-label'
                                        }
                                        for="nameMulti"
                                      >
                                        Password *
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Select
                                        className="React"
                                        classNamePrefix="select"
                                        // defaultValue={colourOptions1[0]}

                                        ref={this.state.selectAssetRef}
                                        name="color"
                                        options={colourOptions1}
                                        isClearable={true}
                                        placeholder="Select Password Type *"
                                        onChange={(e) => {
                                          this.setState({
                                            ptype: e ? e.value : ''
                                          })
                                        }}
                                      />
                                    </FormGroup>
                                    {/* <FormGroup className="form-label-group">
                                      <Input
                                        type="text"
                                        name="description"
                                        id="descriptionMulti"
                                        placeholder="Note"
                                        value={this.state.description}
                                        onChange={(e) => {
                                          this.setState({
                                            description: e.target.value,
                                          });
                                        }}
                                      />
                                      <Label for="descriptionMulti">Note</Label>
                                    </FormGroup> */}
                                  </Col>
                                </Row>
                                <Row>
                                  <Col md="6" sm="12">
                                    {/* <FormGroup className="form-label-group">
                                      <Select
                                        className="React"
                                        classNamePrefix="select"
                                        // defaultValue={colourOptions1[0]}

                                        ref={this.state.selectAssetRef}
                                        name="color"
                                        options={colourOptions1}
                                        isClearable={true}
                                        placeholder="Select Password Type..."
                                        onChange={(e) => {
                                          this.setState({
                                            ptype: e ? e.value : "",
                                          });
                                        }}
                                      />
                                    </FormGroup>*/}
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup
                                      className="form-label-group"
                                      style={{ textAlign: 'right' }}
                                    >
                                      <Button.Ripple
                                        outline
                                        color="secondary"
                                        type="reset"
                                        className="mb-1 button-label"
                                        onClick={(e) => {
                                          this.clearSaved()
                                        }}
                                      >
                                        Reset
                                      </Button.Ripple>
                                      <Button.Ripple
                                        color="warning"
                                        type="reset"
                                        className="ml-1 mb-1 button-label"
                                        // disabled={this.state.userName === '' || this.state.pwd === undefined || this.state.pwd === null || this.state.pwd === ''}
                                        onClick={(e) => {
                                          this.addToSaved(e)
                                        }}
                                      >
                                        {this.state.isLoading && (
                                          <Spinner color="danger" size="sm" />
                                        )}{' '}
                                        {this.state.isLoading
                                          ? ' Adding ..'
                                          : 'Add'}
                                      </Button.Ripple>
                                    </FormGroup>
                                  </Col>
                                </Row>
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                        <Col lg="4" md="6" sm="12">
                          <div>
                            <Swiper {...params}>
                              <div
                                style={{
                                  backgroundImage: `url(${img1})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img2})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img3})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img4})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img5})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                            </Swiper>
                          </div>
                        </Col>
                      </Row>

                      <Row>
                        <ToastContainer />
                        <Col sm="12">
                          <Card>
                            <CardHeader>
                              <CardTitle>Saved Passwords</CardTitle>
                            </CardHeader>
                            <CardBody>
                              <Form>
                                {this.state.saved.length === 0 && (
                                  <h3>No records found!!!</h3>
                                )}
                                {this.state.saved.length > 0 &&
                                  this.state.saved.map((p, idx) => {
                                    console.log('Aaquib p', p)
                                    return (
                                      <Row>
                                        <Col md="3" sm="12">
                                          <FormGroup className="form-label-group">
                                            <Input
                                              className="input-label"
                                              type="text"
                                              name="name"
                                              id="nameMulti"
                                              disabled
                                              value={p.pwdname}
                                              //placeholder="First Name"
                                            />
                                            <Label
                                              className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              }
                                              for="nameMulti"
                                            >
                                              Password For
                                            </Label>
                                          </FormGroup>
                                        </Col>
                                        <Col md="2" sm="12">
                                          <FormGroup className="form-label-group">
                                            <Input
                                              className="input-label"
                                              type="text"
                                              name="name"
                                              id="nameMulti"
                                              disabled
                                              value={p.ptype}
                                              //placeholder="First Name"
                                            />
                                            <Label
                                              className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              }
                                              for="nameMulti"
                                            >
                                              Password Type
                                            </Label>
                                          </FormGroup>
                                        </Col>
                                        <Col md="3" sm="12">
                                          <FormGroup className="form-label-group">
                                            <Input
                                              className="input-label"
                                              type={
                                                p.userName
                                                  ? p.showPass
                                                    ? 'text'
                                                    : 'password'
                                                  : 'text'
                                              }
                                              name="name"
                                              id="nameMulti"
                                              placeholder="User Name"
                                              disabled
                                              value={
                                                p.userName ? p.userName : '  '
                                              }
                                            />
                                            <Label
                                              className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              }
                                              for="nameMulti"
                                            >
                                              User Name
                                            </Label>
                                          </FormGroup>
                                        </Col>
                                        <Col md="2" sm="12">
                                          <FormGroup className="form-label-group">
                                            <Input
                                              className="input-label"
                                              type={
                                                p.showPass ? 'text' : 'password'
                                              }
                                              name="name"
                                              id="nameMulti"
                                              placeholder="Password"
                                              disabled
                                              value={p.pwd}
                                              oncopy="return false"
                                              oncut="return false"
                                              onpaste="return false"
                                            />
                                            <Label
                                              className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              }
                                              for="nameMulti"
                                            >
                                              Password
                                            </Label>
                                          </FormGroup>
                                        </Col>
                                        <Col md="2" sm="12">
                                          <FormGroup className="form-label-group">
                                            {!p.showPass ? (
                                              <Eye
                                                size={20}
                                                onClick={() => {
                                                  if (!this.state.showPass) {
                                                    this.setState({
                                                      showIdx: idx,
                                                      modal: !this.state.modal
                                                    })
                                                  } else {
                                                    let passwordShowTime =
                                                      JSON.parse(
                                                        localStorage.getItem(
                                                          'logInUserData'
                                                        )
                                                      ).pwdtime
                                                    if (
                                                      passwordShowTime > 10 ||
                                                      passwordShowTime < 2
                                                    ) {
                                                      passwordShowTime = 3
                                                    }
                                                    toast.warning(
                                                      'Wait for ' +
                                                        passwordShowTime +
                                                        ' seconds as one password is already visible !'
                                                    )
                                                  }
                                                }}
                                              />
                                            ) : (
                                              <EyeOff
                                                size={20}
                                                onClick={() => {
                                                  this.togglepwdshow(false, idx)
                                                }}
                                              />
                                            )}
                                            <Trash
                                              style={{ marginLeft: '10px' }}
                                              size={20}
                                              className="mr-4 fonticon-wrap"
                                              onClick={() => {
                                                this.deletepwd(idx)
                                              }}
                                            />
                                          </FormGroup>
                                        </Col>
                                      </Row>
                                    )
                                  })}
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                      </Row>
                    </TabPane>
                    <TabPane tabId="2"></TabPane>
                    <TabPane tabId="3">Coming soon .......!</TabPane>
                  </TabContent>
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}
export default PasswordVault
